import React from "react";
import { Link } from "react-router-dom";
import { Container } from "../../globalStyles";

// import linkedinIcon from "../../images/linkedin.svg";
// import githubIcon from "../../images/github.svg";
// import AboutImg from "../../images/heroImg.svg";

import {
  AboutSection,
  AboutRow,
  AboutColumn,
  TextWrapper,
  TopLine,
  SubTitle,
} from "./About.elements";

function About() {
  return (
    <>
      <AboutSection>
        <Container>
          <AboutRow>
            <AboutColumn>
              <TextWrapper>
                <TopLine>Sobre Mi</TopLine>
                <SubTitle>
                  Soy Matias Nuñez Crippa, Analista en Sistemas de Computacion,
                  recibido en la Universidad Nacional de Misiones. Actualmente
                  estoy estudiando Ingenieria en Sistemas de Informacion.
                  Disfruto mucho del desarrollo web, en especifico el desarrollo
                  front-end. Constantemente me encuentro adquiriendo nuevos
                  conocimientos sobre tecnologias y diseño para brindar la mejor
                  experiencia de usuario posible a la hora de desarrollar.
                </SubTitle>
              </TextWrapper>
            </AboutColumn>
          </AboutRow>
        </Container>
      </AboutSection>
    </>
  );
}

export default About;
